import React, { useState, useEffect} from 'react';
import './App.css';

import { Container, Row, Col, Form, OverlayTrigger, Popover } from "react-bootstrap";
import AppSettings, { AppConfigsType, defaultAppConfigs } from "./components/AppSettings";
import Results from "./components/Results";
import { GenericModal, GenericModalConfigsProps } from "./components/GenericModal";
import { loadBypassDisclaimer, loadConfigsFromLocalStorage, saveBypassDisclaimer, saveConfigsToLocalStorage } from './components/localStorageManagement';


var chars = "0123456789abcdefghijklmnopqrstuvwxyz!@#$%^&*()ABCDEFGHIJKLMNOPQRSTUVWXYZ";

var numbers = "0123456789";
var letters = "abcdefghijklmnopqrstuvwxyz";
var capLetters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
var symbols = "!@#$%^&*()_*.:";



const defaultModalConfigs: GenericModalConfigsProps = {
  title: (<>Disclaimer for Use of Password Generation Application</>),
  body: (
    <Row>
      <Col>
        <Form.Control
          as="textarea"
          style={{ height: '60vh' }}
          // type='textArea'
          id="disclaimerText"
          readOnly
          value={`This application is designed to generate passwords according to your preferred configurations. Please be aware that no passwords are stored in any form of long-term memory, storage, or database associated with this application.
                    
The only data retained by this application are the configuration settings you determine. These settings are stored exclusively in your browsers local storage for your convenience.
                        
 By choosing to use this application, you acknowledge and accept the following: The application and its developers bear no responsibility for the creation and usage of any content generated through this application. Your use of the application signifies your understanding and acceptance of these terms.`}
        />
      </Col>
    </Row>
  ),
  cancelBtnText:'',
  submitBtnText: 'Understood',
  showModal: !loadBypassDisclaimer(),
  onSubmitCallBack: () => {saveBypassDisclaimer(true) }
}

function App() {
  const [genPasswordArr, setgenPasswordArr] = useState<string[]>([]);
  const [appConfigs, setappConfigs] = useState<AppConfigsType>(loadConfigsFromLocalStorage() || defaultAppConfigs);

  const [genericModalData, setGenericModalData] = useState<GenericModalConfigsProps>(defaultModalConfigs)

  const handleModalState = (state: boolean) => { setGenericModalData({ ...genericModalData, showModal: state }) }


  // const [appConfigs2, dispatchAppConfigs] = useReducer(appConfigsReducer, defaultAppConfigs);


  const changeAppConfigs = (newAppConfigs: AppConfigsType) => {
    console.log("changing app configs: ", newAppConfigs)
    setappConfigs(newAppConfigs)

  }

  //Initiates the process to generate passwords
  function generatePassword() {
    let _genPasswordArr = [];
    let generationAmt: number = appConfigs.amtOfPasswords[0];
    let passwordLengthRange: number[] = appConfigs.passwordLengthRange
    let useWordsList = appConfigs.wordsList.useWordsList
    let substituteVowels = appConfigs.wordsList.substituteVowels
    let wordsList = appConfigs.wordsList.dictionary

    let charFilter = appConfigs.charFilter;



    // var characters = "0123456789abcdefghijklmnopqrstuvwxyz!@#$%^&*()ABCDEFGHIJKLMNOPQRSTUVWXYZ";

    let characters = ((charFilter.useLetters ? letters : "") +
      (charFilter.useCAPS ? capLetters : "") +
      (charFilter.useSymbols ? symbols : "") +
      (charFilter.useNumbers ? numbers : "") +
      (charFilter.useCustom ? charFilter.customChars : "")).replaceAll(' ', '')


    if (characters == "" && wordsList.length < 0) {
      return
    }



    //Shuffle characters in a provided string
    function shuffleChars(characters: string): string {
      var a = characters.split(""),
        n = a.length;

      for (var i = n - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var tmp = a[i];
        a[i] = a[j];
        a[j] = tmp;
      }
      return a.join("");
    }

    characters = shuffleChars(characters);
    //console.log("Shuffled result: ", characters, generationAmt)
    //Filters words from array of string, where string length is less than or equal to provided maxLength
    function findShortWords(words: string[], maxLength: number) {
      return words.filter(word => word.length < maxLength);
    }

    //Chooses a random word from array of strings
    function chooseWords(_wordsArr: string[]) {
      //Choose random word from array
      var randomNumber = Math.floor(Math.random() * wordsArr.length); //Generate random Number based on the length of the array
      //console.error(_wordsArr[randomNumber])
      return (_wordsArr[randomNumber])
    }

    //Substitutes certain Vowels with hardcoded symbols 
    function subVowels(word: string) {
      //Note: May allow User the ability to configure in the future.
      var finalword = "";
      word.split('').forEach(element => {
        const char = element.toLowerCase();
        switch (char) {
          case 'a':
            finalword += '@'
            break;
          case 'e':
            finalword += '3'
            break;
          case 'i':
            finalword += '!'
            break;
          case 'o':
            finalword += '0'
            break;
          case 'u':
            finalword += ']'
            break;
          default:
            finalword += element
            break;
        }
      });
      return finalword
    }

    //Choose a number of random characters from a string of characters, up to the maximum specified amount
    function selectRandomCharacters(_characters: string, maximum: number, strictMax: boolean = true) {
      var stringOfRandomChars = ""
      const trueMax = strictMax ? maximum : Math.floor(Math.random() * maximum); //if strictMax is true use the maximum value,or else use random value up to the provided maximum
      console.log("truemax: ", trueMax)
      for (var i = 0; i <= trueMax; i++) {
        var randomNumber = Math.floor(Math.random() * _characters.length);//Generate random number from 0 to maximum
        stringOfRandomChars += _characters.substring(randomNumber, randomNumber + 1);
      }
      return stringOfRandomChars

    }

    //Generate passowrd up to the specified amount{generationAmt}
    for (let index = 0; index < generationAmt; index++) {
      let password = "";
      let max = passwordLengthRange[1]
      let min = passwordLengthRange[0] - 1;
      let tempMaxChar = Math.floor(Math.random() * (max - min)) + min;
      //Check if useWordsList is true 
      if (useWordsList) {
        //If word List is enabled then:
        var wordsArr = wordsList.split(',');// Create an array of String {wordsList}, from {wordsList}, spliting at each comma ','
        wordsArr = findShortWords(wordsArr, max) //Get an array of String of words with character lengths that fall within the {max}


        if (wordsArr.length > 0) {

          var counter: number = 0 //Counter to represent the maximum amount of iterations allowed by following While Loop
          while (counter <= 5) {
            let text = chooseWords(wordsArr);
            if ((password + text).length > max) {

            } else {

              password += substituteVowels ? subVowels(text) : text;
              wordsArr = wordsArr.filter(word => word !== text); //Remvoe the word so it is not duplicated
            }

            if ((password + text).length == max) {
              counter = 6

            } else {

              //Between each word:
              password += selectRandomCharacters(characters, Math.floor((max - password.length) / 2), false) //Characters are generated between each word. A random amount between 0 and 1/2 of the remaining character spaces.
              console.log('Password:', password)
            }
            counter++;
          }
        }
        console.log(password, password.length)
      } else {
        //If word List is not enabled then :
        password += selectRandomCharacters(characters, tempMaxChar)

      }

      //If the password length is less than 1(Most likely to occure if using the Word List), then generate using random characters 
      if (password.length < 1) {
        password += selectRandomCharacters(characters, tempMaxChar)
      }

      // password = password.replaceAll(' ', '')
      // password += "-" + password.length.toString() // Adds the length at the end of the password for testing
      _genPasswordArr.push(password)
    }


    console.log(_genPasswordArr, appConfigs);
    setgenPasswordArr(_genPasswordArr);
  }


  useEffect(() => {
    //generatePassword();



  }, []);
  useEffect(() => {
    const elementArr = document.getElementById('AppSettingsContainer')?.querySelectorAll('.Input-NotValid')
    if (elementArr!.length <= 0) {
      //Checks to make sure there are not invalid configs before trying to save
      saveConfigsToLocalStorage(appConfigs)

    }

  }, [appConfigs]);

  useEffect(() => {
  });
  return (

    <div className="App">
      <GenericModal configs={genericModalData} handleModalState={handleModalState} />
      <div className="App-Body">
        <Container fluid className='px-md-5 mx-md-5' >
          <Row className=' justify-content-center pb-5'>
            <Col className=' align-items-center text-center'>
              <h1>Password Generator</h1>
            </Col>
          </Row>
          <Row className=' justify-content-center pb-5'>
            <Col id="AppSettingsContainer" md='6' xl='5' className=' align-items-center'>
              <AppSettings
                appConfigs={appConfigs}
                changeAppConfigs={changeAppConfigs}
                genPassword={() => generatePassword()}
                setGenericModalData={(data) => setGenericModalData({ ...genericModalData, ...data })}
              />
            </Col>
            <Col id="ResultsContainer">
              <Results genPasswordArr={genPasswordArr} />
            </Col>
          </Row>


        </Container>

      </div>






      <footer className="footer mt-auto py-3 bg-dark" >
        <Container fluid className='' >
          <Row className='d-flex justify-content-center'>
            <Col className=' align-items-center text-center'>

              <OverlayTrigger overlay={
                <Popover >
                  <Popover.Header as="h3">{`Application under Development`}</Popover.Header>
                  <Popover.Body>
                    Currently, this project is in its development phase and is actively undergoing rigorous testing to ensure its functionality and reliability.
                    <br />
                    Once the development and testing phases are successfully completed, the application will transition into full production. Upon reaching this milestone, More Detailed information will be revealed about the application on my GitHub page.

                    {/* This will include comprehensive documentation, feature descriptions, usage instructions, and more. We believe in the power of open-source and community collaboration, and we look forward to sharing our work with the wider community on GitHub. */}
                  </Popover.Body>
                </Popover>
              }
              >

                <span className=".text-white" style={{ color: 'white', cursor: 'pointer' }}>
                  At present, this project is in the development stage and undergoing testing.
                  {/* <a href="https://github.com/michaelj1297/Bingo-PDF-Generator.git">github repo</a>. */}
                </span>
              </OverlayTrigger>



            </Col>
          </Row>


        </Container>
      </footer>
    </div>
  );
}

export default App;


