import { defaultAppConfigs, AppConfigsType } from "./AppSettings";


function saveConfigsToLocalStorage(appConfigs: AppConfigsType) {

  const appConfigs_JSON = JSON.stringify(appConfigs);
  localStorage.setItem("PassGen_AppConfigs", appConfigs_JSON);


  return;

  //Below Code was the 1st version.


  localStorage.setItem("amtOfPasswords", appConfigs.amtOfPasswords.toString());
  localStorage.setItem("passwordLengthRangeMin", appConfigs.passwordLengthRange[0].toString());
  localStorage.setItem("passwordLengthRangeMax", appConfigs.passwordLengthRange[1].toString());

  localStorage.setItem("useLetters", appConfigs.charFilter.useLetters.toString());
  localStorage.setItem("useNumbers", appConfigs.charFilter.useNumbers.toString());
  localStorage.setItem("useCAPS", appConfigs.charFilter.useCAPS.toString());
  localStorage.setItem("useSymbols", appConfigs.charFilter.useSymbols.toString());
  localStorage.setItem("useCustom", appConfigs.charFilter.useCustom.toString());
  localStorage.setItem("customChars", appConfigs.charFilter.customChars.toString());

  localStorage.setItem("wordsList", appConfigs.wordsList.dictionary);
  localStorage.setItem("useWordsList", appConfigs.wordsList.useWordsList.toString());
  localStorage.setItem("substituteVowels", appConfigs.wordsList.substituteVowels.toString());

}

function loadConfigsFromLocalStorage(): AppConfigsType {

  const oldLSItemNames = "passwordLengthRangeMin,passwordLengthRangeMax,amtOfPasswords,useLetters,useNumbers,useCAPS,useSymbols,useCustom,customChars,wordsList,useWordsList,substituteVowels"; //List of Local Storage Item Names from earlier App Version to be used below to transition to format for the new version




  try {
    const appConfigs_JSON = localStorage.getItem("PassGen_AppConfigs")?.toString() || "";
    var appConfigs: AppConfigsType = defaultAppConfigs;
   
    //If localStorage {PassGen_AppConfigs} is empty/Null, then check for items in the older storages
    if (appConfigs_JSON == "") {
      var c = 0


      oldLSItemNames.split(',').every(element => {
        if (localStorage.getItem(element) != null) {
          // If an item was found under the old Local Storage ItemName, then add to counter and break
          c++;
          return false
        }
        return true
      });



      if (c >= 1) {
        //If true then an item was found under the old Local Storage Item Name so, retrieve values, save to new format, then delete the old.
        //To Be Removed in a future version
        try {
          appConfigs = {
            passwordLengthRange: [parseInt(localStorage.getItem("passwordLengthRangeMin")!) || 8, parseInt(localStorage.getItem("passwordLengthRangeMax")!) || 15],
            amtOfPasswords: [parseInt(localStorage.getItem("amtOfPasswords")!) || 5],
            charFilter: {
              useLetters: localStorage.getItem("useLetters") == 'true' ? true : false || true,
              useNumbers: localStorage.getItem("useNumbers") == 'true' ? true : false || true,
              useCAPS: localStorage.getItem("useCAPS") == 'true' ? true : false || true,
              useSymbols: localStorage.getItem("useSymbols") == 'true' ? true : false || true,
              useCustom: localStorage.getItem("useCustom") == 'true' ? true : false || false,
              customChars: localStorage.getItem("customChars")! || "",
            },
            wordsList: {
              dictionary: localStorage.getItem("wordsList") || "",
              useWordsList: localStorage.getItem("useWordsList") == 'true' ? true : false || false,
              substituteVowels: localStorage.getItem("substituteVowels") == 'true' ? true : false || false
            },
          }
        } catch (error) {
        }
        saveConfigsToLocalStorage(appConfigs); // Save to new LocalStorage Names

        //Remove Values from old Local Storage Names
        oldLSItemNames.split(',').forEach(element => {
          localStorage.removeItem(element);
        })
      }


    }
    else {
      appConfigs = JSON.parse(appConfigs_JSON) as AppConfigsType
    }

    return appConfigs
  } catch (ex) {
    return defaultAppConfigs
  }




}
function DeleteConfigsFromLocalStorage() {
  //Clear all of the data from PassGen_AppConfigs in Local Storage
  localStorage.removeItem('PassGen_AppConfigs')
  clearBypassDisclaimer(); 
}



function loadBypassDisclaimer(): boolean {
  try {
    const bypassDisclaimer: boolean = (localStorage.getItem("PassGen_BypassDisclaimer") == 'true' ? true : false) || false
    // const showDisclaimer: boolean = JSON.parse(showDisclaimer_JSON) as boolean

    return bypassDisclaimer
  } catch (ex) {
    return true
  }
}
function saveBypassDisclaimer(showDisclaimer: boolean) {
  localStorage.setItem("PassGen_BypassDisclaimer", showDisclaimer.toString());
}


function clearBypassDisclaimer() {
  localStorage.removeItem('PassGen_BypassDisclaimer')
}




export {
  saveConfigsToLocalStorage, loadConfigsFromLocalStorage, DeleteConfigsFromLocalStorage,
  loadBypassDisclaimer, saveBypassDisclaimer, clearBypassDisclaimer

}