import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';


interface GenericModalConfigsProps {
    title: React.ReactElement,
    body: React.ReactElement,
    showModal: boolean,
    submitBtnText: string,
    cancelBtnText:string

    onSubmitCallBack: Function
}
// React.Dispatch<React.SetStateAction<GenericModalConfigsProps>>

function GenericModal(props: {
    configs: GenericModalConfigsProps,
    handleModalState: Function
}) {
    const { configs: { title, body, showModal }, handleModalState } = props;
    const submitBtnText = props.configs.submitBtnText || 'Save Changes'
    const cancelBtnText = props.configs.cancelBtnText 

    
    const onSubmitCallBack = props.configs.onSubmitCallBack;

    // const [show, setShow] = useState(false);

    const handleClose = () => handleModalState(false);
    const handleShow = () => handleModalState(true);

    return (
        <>
            {/* <Button variant="primary" onClick={handleShow}>
                Launch demo modal
            </Button> */}

            <Modal show={showModal} onHide={handleClose} backdrop={"static"}>
                <Modal.Header closeButton={cancelBtnText.length >0}>
                    <Modal.Title>{title}</Modal.Title>
                </Modal.Header>
                <Modal.Body>{body}</Modal.Body>
                <Modal.Footer>

                    {cancelBtnText.length >0 &&
                    <Button variant="secondary" onClick={handleClose}>
                        {cancelBtnText}
                    </Button>
                }

                    <Button variant="primary" onClick={() => { onSubmitCallBack();  handleClose()} }>
                        {submitBtnText}
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export { GenericModal, type GenericModalConfigsProps };