import React, { useState, useEffect } from 'react';

import { Container, Row, Col, OverlayTrigger, Popover, ListGroup, Button, Form, InputGroup, Navbar } from "react-bootstrap";



type ResultsProps = {
    genPasswordArr: string[]

}


function Results(props: ResultsProps) {
    const [selectedRows, setselectedRows] = useState<boolean[]>([false]);
    const [hidePW, sethidePW] = useState(true);
    const genPasswordArr = props.genPasswordArr;

    const _tempSelectedRows: boolean[] = [];
    genPasswordArr.map((element, index) => {
        _tempSelectedRows.push(false)

    })
    useEffect(() => {
        setselectedRows(_tempSelectedRows);
    }, [genPasswordArr]);



    function toggleRowSelection(id: number) {
        var _selectedRows: boolean[] = [...selectedRows];
        // _selectedRows[id] = !_selectedRows[id];

        //Sets all Values in {_selectedRows} to a specified boolean state
        function setAllValues(state: boolean) {
            for (let i = 0; i < _selectedRows.length; i++) {
                _selectedRows[i] = state;
            }
        }

        //If selected id is 0, then proceed with logic to select or deselect all
        if (id == 0) {
            //Selected id is 0
            if (_selectedRows[id]) {
                //First Element, SelectAll, is true so deselect all by changing to false.
                setAllValues(false);
            } else {
                //First Element, SelectAll, is false so select all by changing to true.
                setAllValues(true);
            }
        } else {
            //Selected id is not 0
            //Selected id is 0
            _selectedRows[0] = false;
            _selectedRows[id] = !_selectedRows[id];

        }


        console.log(_selectedRows)
        setselectedRows(_selectedRows)
        // setselectedRows
    }



    const popover = (props: { header: string, body: string }) => (
        <Popover id="popover-basic">
            {props.header != null &&
                <Popover.Header as="h3">{props.header}</Popover.Header>
            }
            <Popover.Body>
                {props.body}<br />
            </Popover.Body>
        </Popover>
    );

    function saveToClipboard(id: number) {
        console.log(id)
        if (id == null || id == undefined) {
            alert('There was an error saving to clipboard!')
        } else if (id == 0 && selectedRows.includes(true)) {
            var passList: string = "";
            var passListId: string = "";

            var c = 1
            selectedRows.forEach((element, index) => {
                if ((selectedRows[0] || (element)) && index >= 1) {
                    passList += (c > 1 ? ',\n' : '') + genPasswordArr[index - 1]
                    passListId += (c > 1 ? ',' : '') + index
                    c++
                }
            });
            console.log(passList);
            navigator.clipboard.writeText(passList);
            alert(`Passwords ${passListId}, saved to clipboard`);
        } else if (genPasswordArr[id - 1] != undefined || genPasswordArr[id - 1] != null) {
            navigator.clipboard.writeText(genPasswordArr[id - 1]);
            alert(`Password ${id} saved to clipboard`);
        } else {
            alert('No Selection was made!')

        }

    }

    return (<>
        <Row className='justify-content-center mt-s-5'>
            <Col lg='11' className='text-center'>
                <h1>Results</h1>
            </Col>
        </Row>
        
        {genPasswordArr.length > 0 &&
            <>
                <Row className='mt-xs-5'>

                    <Col className='text-center'>
                        <div className='input-group mb-3'>

                            <OverlayTrigger overlay={popover({ header: "", body: 'Select all Passwords' })}>

                                <button type="button" className="btn btn-light"><i className={selectedRows[0] ? "bi bi-square-fill" : "bi bi-square"}
                                    onClick={() => toggleRowSelection(0)}
                                ></i></button>
                            </OverlayTrigger>

                            <OverlayTrigger overlay={popover({ header: '', body: `Click to ${hidePW ? "Show" : "Hide"} Passwords.` })}>
                                <button className="btn " type="button" onClick={() => sethidePW(!hidePW)}>
                                    <i className={hidePW ? "bi bi-eye-slash-fill" : "bi bi-eye-fill test"}></i>
                                </button>

                            </OverlayTrigger>


                            <input
                                className="form-control text-center" style={{ fontSize: 'calc(14px + 2vmin)', fontWeight: 'bold' }}
                                value={''} readOnly />
                            {selectedRows[0] &&
                                <label className="input-group-text ">

                                    <OverlayTrigger overlay={popover({ header: 'Save Selection to Clipboard', body: 'Save all of the selected passwords to Clipboard.' })}>
                                        <button className="btn " type="button" onClick={() => saveToClipboard(0)}><i className="bi bi-clipboard"></i></button>

                                    </OverlayTrigger>
                                </label>}

                        </div>

                    </Col>
                </Row>

            </>
        }


        <div id='resultsList'>
            {genPasswordArr.map((element, index) => (

                <Row>
                    <Col>

                        <div className='input-group mb-3'>
                            <OverlayTrigger overlay={popover({ header: "", body: 'Select password #' + (index + 1) })}>
                                <button type="button" className="btn btn-light"
                                    onClick={() => toggleRowSelection(index + 1)}>
                                    <i className={selectedRows[index + 1] || selectedRows[0] ? "bi bi-square-fill" : "bi bi-square"} />
                                </button>
                            </OverlayTrigger>

                            <label className="input-group-text " >{index + 1}</label>

                            <input
                                className="form-control"
                                type={hidePW ? "password" : "input"}
                                value={element} />
                            <label className="input-group-text ">
                                <OverlayTrigger overlay={popover({ header: 'Save to Clipboard', body: 'Save this password to Clipboard.' })}>
                                    <button className="btn " type="button" onClick={() => saveToClipboard(index + 1)}><i className="bi bi-clipboard"></i></button>

                                </OverlayTrigger>



                            </label>

                        </div>

                    </Col>
                </Row>
            ))}
        </div >
    </>
    );
}

export default Results;